html {
  height: 100%;
  overflow: hidden; }

body {
  overflow: hidden;
  margin: 0;
  padding: 0;
  font-family: Raleway, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(top left, #0c7b93, #142850);
  background: linear-gradient(to bottom right, #0c7b93, #142850);
  min-height: 100%;
  display: -webkit-flex;
  display: flex; }

#root {
  display: -webkit-flex;
  display: flex;
  width: 100%; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

#app {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  position: relative; }
  #app > canvas {
    position: fixed;
    top: 0;
    left: 0;
    -webkit-transition: all 5000ms;
    transition: all 5000ms;
    width: 100vw !important;
    height: 200vh !important; }
  #app.goneWave > canvas {
    -webkit-transform: translateY(100%);
            transform: translateY(100%); }

.enter {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 2;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-transition: all 1000ms cubic-bezier(0.895, 0.03, 0.685, 0.22);
  transition: all 1000ms cubic-bezier(0.895, 0.03, 0.685, 0.22); }
  .enter.done {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    opacity: 0; }
  .enter > input {
    display: -webkit-flex;
    display: flex;
    background: transparent;
    -webkit-flex: 0 1;
            flex: 0 1;
    font-family: Raleway;
    border-style: none;
    font-size: 2.5rem;
    position: relative;
    width: 100%;
    z-index: 2;
    color: #FFF;
    text-align: center;
    width: 100%;
    -webkit-transition: all 200ms;
    transition: all 200ms; }
    .enter > input:focus {
      outline: 0; }
    .enter > input.valid {
      opacity: 0.5; }
    .enter > input:disabled {
      font-size: 1.5rem; }
  .enter > div {
    margin-top: 2rem;
    display: -webkit-flex;
    display: flex;
    opacity: 0.2;
    -webkit-transition: all 1s cubic-bezier(0.19, 1, 0.22, 1);
    transition: all 1s cubic-bezier(0.19, 1, 0.22, 1);
    -webkit-transform: scale(0.8);
            transform: scale(0.8); }
    .enter > div.valid {
      -webkit-transform: scale(1);
              transform: scale(1);
      opacity: 1; }
    .enter > div button {
      font-size: 1.2rem;
      margin: 0 0.25rem;
      padding: 0.5rem 1rem;
      border-radius: 4px;
      background: transparent;
      color: white;
      cursor: pointer;
      font-weight: 500;
      border: 3px solid transparent;
      -webkit-transition: all 500ms cubic-bezier(0.19, 1, 0.22, 1);
      transition: all 500ms cubic-bezier(0.19, 1, 0.22, 1);
      -webkit-transform: scale(1);
              transform: scale(1); }
      .enter > div button:hover {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
        border-color: rgba(255, 255, 255, 0.5); }
      .enter > div button:disabled {
        -webkit-transform: scale(1) !important;
                transform: scale(1) !important;
        border-color: transparent !important;
        cursor: default; }
      .enter > div button:focus, .enter > div button:active {
        outline: 0; }

.fileContainer {
  position: relative;
  overflow: hidden;
  display: inline-block; }

.fileContainer .btn {
  border: 2px solid rgba(0, 0, 0, 0.8);
  color: white;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 8px 20px;
  margin-bottom: 30px;
  border-radius: 8px;
  font-size: 3rem;
  font-weight: bold; }

.fileContainer .btn:disabled {
  font-size: 1.5rem; }

.fileContainer .btn:hover {
  border: 10px solid rgba(0, 0, 0, 0.8);
  color: white;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 8px 20px;
  border-radius: 8px;
  font-size: 20px;
  font-weight: bold; }

.fileContainer input[type=file] {
  font-size: 100px;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0; }

@-webkit-keyframes entrance {
  from {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    opacity: 0; }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1; } }

@keyframes entrance {
  from {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    opacity: 0; }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1; } }

.video-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  width: 100%;
  top: 0;
  -webkit-animation-name: entrance;
          animation-name: entrance;
  height: 100%;
  z-index: 2;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  position: relative;
  -webkit-animation-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
          animation-timing-function: cubic-bezier(0.19, 1, 0.22, 1); }
  .video-container > .loader {
    position: relative;
    display: block;
    width: 100%;
    text-align: center; }

@-webkit-keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }
    .video-container > .loader .spinner:before {
      content: '';
      box-sizing: border-box;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 40px;
      height: 40px;
      margin-top: -50px;
      margin-left: -20px;
      border-radius: 50%;
      border-top: 2px solid rgba(255, 255, 255, 0.9);
      border-right: 2px solid transparent;
      -webkit-animation: spinner 2s linear infinite;
              animation: spinner 2s linear infinite; }
    .video-container > .loader p {
      text-align: center;
      padding-top: 40px;
      color: rgba(255, 255, 255, 0.8); }
  .video-container > video {
    position: absolute;
    top: -100%;
    left: 10%;
    background: black;
    pointer-events: none;
    opacity: 0;
    z-index: -1;
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.2);
    width: 80%;
    height: 80%; }
  .video-container.ready > video {
